module.exports = "import { Component, OnInit } from '@angular/core';\nimport { NzFormatEmitEvent, NzTreeNode } from 'ng-zorro-antd';\n\n@Component({\n  selector: 'nz-demo-tree-select-async',\n  template: `\n    <nz-tree-select style=\"width: 250px\"\n      nzPlaceHolder=\"Please select\"\n      [nzDefaultExpandedKeys]=\"expandKeys\"\n      [nzDropdownMatchSelectWidth]=\"true\"\n      [nzDropdownStyle]=\"{ 'max-height': '300px' }\"\n      [(ngModel)]=\"value\"\n      [nzNodes]=\"nodes\"\n      [nzAsyncData]=\"true\"\n      (nzExpandChange)=\"onExpandChange($event)\">\n    </nz-tree-select>\n  `\n})\n\nexport class NzDemoTreeSelectAsyncComponent implements OnInit {\n  expandKeys = [ '0-0' ];\n  value: string;\n  nodes = [ {\n    title   : 'Node1',\n    value   : '0-0',\n    key     : '0-0',\n    children: [ {\n      title: 'Child Node1',\n      value: '0-0-1',\n      key  : '0-0-1'\n    }, {\n      title: 'Child Node2',\n      value: '0-0-2',\n      key  : '0-0-2'\n    } ]\n  }, {\n    title: 'Node2',\n    value: '0-1',\n    key  : '0-1'\n  } ];\n\n  onExpandChange(e: NzFormatEmitEvent): void {\n    if (e.node.getChildren().length === 0 && e.node.isExpanded) {\n      this.loadNode().then(data => {\n        e.node.addChildren(data);\n      });\n    }\n  }\n\n  loadNode(): Promise<any[]> {\n    return new Promise(resolve => {\n      setTimeout(() => resolve([\n          { title: 'Child Node', key: `${(new Date()).getTime()}-0` },\n          { title: 'Child Node', key: `${(new Date()).getTime()}-1` } ]),\n        1000);\n    });\n  }\n\n  ngOnInit(): void {\n  }\n}\n"