module.exports = "import { Component, OnInit } from '@angular/core';\nimport { NzFormatEmitEvent, NzTreeNode } from 'ng-zorro-antd';\n\n@Component({\n  selector: 'nz-demo-tree-select-checkable',\n  template: `\n    <nz-tree-select\n      style=\"width: 250px\"\n      [nzNodes]=\"nodes\"\n      nzShowSearch\n      nzCheckable\n      nzPlaceHolder=\"Please select\"\n      [(ngModel)]=\"value\"\n      (ngModelChange)=\"onChange($event)\">\n    </nz-tree-select>`\n})\n\nexport class NzDemoTreeSelectCheckableComponent implements OnInit {\n\n  value: string[] = [ '0-0-0' ];\n  nodes = [ {\n    title   : 'Node1',\n    value   : '0-0',\n    key     : '0-0',\n    children: [ {\n      title : 'Child Node1',\n      value : '0-0-0',\n      key   : '0-0-0',\n      isLeaf: true\n    } ]\n  }, {\n    title   : 'Node2',\n    value   : '0-1',\n    key     : '0-1',\n    children: [ {\n      title : 'Child Node3',\n      value : '0-1-0',\n      key   : '0-1-0',\n      isLeaf: true\n    }, {\n      title : 'Child Node4',\n      value : '0-1-1',\n      key   : '0-1-1',\n      isLeaf: true\n    }, {\n      title : 'Child Node5',\n      value : '0-1-2',\n      key   : '0-1-2',\n      isLeaf: true\n    } ]\n  } ];\n\n  onChange($event: string[]): void {\n    console.log($event);\n  }\n\n  ngOnInit(): void {\n  }\n}\n"